import React from 'react'
import { useFeatureTranslations } from '../../../../hooks/useAutoTranslations';

export const Header = () => {
    // Usar el hook más confiable especificando feature y componente
    const t = useFeatureTranslations('users', 'header');
  return (
    <div>Header
        {t('title')}
    </div>

  )
}
