import { useTranslations } from 'next-intl';
import { useMemo } from 'react';

/**
 * Hook personalizado que automáticamente detecta el namespace de traducciones
 * basado en la ubicación del archivo que lo llama.
 * 
 * Como alternativa más confiable, también acepta un namespace manual.
 * 
 * @param manualNamespace - (Opcional) Namespace manual para casos específicos
 * @returns Función de traducción configurada con el namespace automático
 */
export function useAutoTranslations(manualNamespace?: string) {
  const namespace = useMemo(() => {
    // Si se proporciona un namespace manual, usarlo
    if (manualNamespace) {
      return manualNamespace;
    }

    // Intentar detectar automáticamente usando el stack trace
    try {
      const error = new Error();
      const stack = error.stack;
      
      if (!stack) {
        console.warn('useAutoTranslations: No se pudo obtener el stack trace');
        return '';
      }

      // Buscar la línea del stack que corresponde al archivo que llama al hook
      const stackLines = stack.split('\n');
      
      for (let i = 2; i < stackLines.length; i++) {
        const line = stackLines[i];
        
        // Buscar patrones de archivo en el stack trace
        const fileMatch = line.match(/at.*\((.+):(\d+):(\d+)\)/) || line.match(/at (.+):(\d+):(\d+)/);
        
        if (fileMatch) {
          const filePath = fileMatch[1];
          
          // Filtrar archivos internos y buscar archivos en screens
          if (filePath && 
              !filePath.includes('node_modules') && 
              !filePath.includes('useAutoTranslations') &&
              !filePath.includes('react-dom') &&
              !filePath.includes('scheduler') &&
              !filePath.includes('webpack') &&
              filePath.includes('screens')) {
            
            // Generar namespace basado en la ruta
            const normalizedPath = filePath.replace(/\\/g, '/');
            const screensMatch = normalizedPath.match(/screens\/([^\/]+)\/(.+)/);
            
            if (screensMatch) {
              const feature = screensMatch[1]; // ej: "users"
              const subPath = screensMatch[2]; // ej: "components/Header/Header.tsx"
              
              // Dividir el subPath y filtrar partes no deseadas
              const pathParts = subPath.split('/');
              pathParts.pop(); // Remover el archivo final
              
              // Filtrar "components" y convertir a lowercase
              const namespaceParts = pathParts
                .filter(part => part !== 'components')
                .map(part => part.toLowerCase());
              
              // Construir el namespace final
              const detectedNamespace = [feature.toLowerCase(), ...namespaceParts].join('.');
              
              return detectedNamespace;
            }
          }
        }
      }
    } catch (error) {
      console.error('useAutoTranslations: Error en detección automática:', error);
    }
    
    return '';
  }, [manualNamespace]);

  // Si no se pudo detectar el namespace, mostrar advertencia
  if (!namespace) {
    console.warn('useAutoTranslations: No se pudo detectar el namespace automáticamente. Considera usar el parámetro manual.');
  }

  return useTranslations(namespace);
}

/**
 * Hook alternativo más simple que requiere especificar el feature y componente
 * 
 * @param feature - Nombre del feature (ej: 'users')
 * @param component - Nombre del componente (ej: 'header')
 * @returns Función de traducción configurada
 */
export function useFeatureTranslations(feature: string, component?: string) {
  const namespace = component ? `${feature.toLowerCase()}.${component.toLowerCase()}` : feature.toLowerCase();
  return useTranslations(namespace);
}

/**
 * Hook que construye el namespace basado en la estructura de carpetas
 * Este es el más confiable y recomendado
 * 
 * @param parts - Partes del namespace (ej: ['users', 'header'])
 * @returns Función de traducción configurada
 */
export function useNamespaceTranslations(...parts: string[]) {
  const namespace = parts.map(part => part.toLowerCase()).join('.');
  return useTranslations(namespace);
}
